import Constants from '../constants';

const topLevelActions = [
  Constants.TOP_ACTION_LABEL.CREATEWORK,
  Constants.TOP_ACTION_LABEL.OPENASSIGNMENT,
  Constants.TOP_ACTION_LABEL.FINISHASSIGNMENT,
  Constants.TOP_ACTION_LABEL.OPENWORKBYHANDLE,
  Constants.TOP_ACTION_LABEL.SAVEANDCLOSE,
  Constants.TOP_ACTION_LABEL.SHOWDATA
];

/**
 * This API forms actionsInfo consists of topLevelAction flag and actionLabel and conditional assignmentID.
 * @param {label} label - Top action label.
 * @param {string} assignmentID - Assignment ID for open assignment action
 *
 * @returns {actionsInfo}  - Action Info object consists of topLevelAction flag and actionLabel and conditional assignmentID
 */
const performanceUtils = {
  getActionInfo: (label: string, assignmentID?: string) => {
    let actionInfo: Record<string, string | boolean | undefined> = {};
    if (topLevelActions.includes(label)) {
      actionInfo = {
        topLevelAction: true,
        actionLabel: label
      };
    }
    if (label === Constants.TOP_ACTION_LABEL.OPENASSIGNMENT) {
      actionInfo.assignmentID = assignmentID;
    }
    return actionInfo;
  }
};

export default performanceUtils;
