import { AxiosError } from 'axios';
import type { AxiosResponse } from 'axios';

import PegaConstants from '../constants';
import PubSubUtils from '../utils/pubsub-utils';
// eslint-disable-next-line import/no-cycle
import Utils from '../utils/utils';
import Services from '../services';
import { getValue } from '../store/state/state-manager';

import type { Action, ActionsInfo, ResponseConfig, ServiceCall } from './performance-manager.types';

/**
 * @description
 * @private
 * Exposes APIs to capture performances of various network calls
 * and categorize it according to type of network calls and user actions
 */

class PerformanceManager {
  static readonly portalRegex = /(?<=v2\/pages\/)(.*?)(?=\?pageClass=)/;
  #startTime = 0;
  #endTime = 0;
  #togglePerformanceLog: boolean;
  #actionsMasterList: Action[] = [];

  constructor() {
    this.#togglePerformanceLog = false;
    PubSubUtils.subscribe(
      PegaConstants.START_PERFORMANCE_LOG,
      () => {
        this.#togglePerformanceLog = true;
        this.clearStorage();
        this.#startTime = Date.now();
      },
      PegaConstants.START_PERFORMANCE_LOG
    );

    PubSubUtils.subscribe(
      PegaConstants.STOP_PERFORMANCE_LOG,
      () => {
        this.#togglePerformanceLog = false;
        this.#endTime = Date.now();
        this.#updateLocalStorage();
      },
      PegaConstants.STOP_PERFORMANCE_LOG
    );
  }

  /**
   * Returns Category from url
   * @description This method returns Category from url
   * @param {string} url - refreshMap or propChangeRefreshMap passed
   *
   * @example <caption>Example for getCategory</caption>
   * This is an internal to Performance manager.
   * Example usage - PerformanceManager.getCategory('/api/application/v2/cases?viewType=page')
   *
   * @see {@link getCategory} for further information.
   * @returns {string}
   * @function
   */
  static getCategory(url: string): string {
    if (url.includes('v2/cases') || url.includes('v2/assignments')) {
      return 'Case';
    }
    if (url.includes('v2/data_views')) {
      return 'DataPage';
    }
    if (url.includes('v2/pages')) {
      return 'Page';
    }
    return '';
  }

  /**
   * Returns portal label from any portal ID
   * @description This method returns label of a portal by iterating through redux within available portals
   * @param {string} portalID - ID of the portal being launched
   * @param {string} context - context where portal is launched
   *
   * @example <caption>Example for getPortalLabel</caption>
   * This is internal to Performance manager.
   * Example usage - PerformanceManager.getPortalLabel('pyHome', 'app')
   *
   * @see {@link getPortalLabel} for further information.
   * @returns {string}
   * @function
   */
  static getPortalLabel(portalID: string, context: string): string {
    const portals = getValue('.pyPrimaryNavPages', 'pyPortal', context) || [];
    return portals.find((portal: Record<string, string>) => portal.pyRuleName === portalID)?.pyLabel ?? portalID;
  }

  /**
   * Extracts assignment label for Open assignment top level action and attaches it to action object
   * @description This method extracts label of assignment by iterating through response of Open assignment within assignments array
   * @param {Action} action - action info of any top level action
   * @param {string} assignmentID - ID of the assignment being launched
   * @param {AxiosResponse} response - response of Open assignment
   *
   * @example <caption>Example for extractAssignmentLabel</caption>
   * This is internal to Performance manager.
   * Example usage - PerformanceManager.extractAssignmentLabel({
   *   label: 'Open-Assignment',
   *   actionUniqueID: '6092549c',
   *   assignmentLabel: 'Create'
   * }, 'ASSIGN-WORKLIST O8EQL4-REACT-WORK S-3016!CREATEFORM_DEFAULT', openAssignmentResponse)
   *
   * @see {@link extractAssignmentLabel} for further information.
   * @returns {void}
   * @function
   */
  static extractAssignmentLabel(action: Action, assignmentID: string, response: AxiosResponse): void {
    if (action.label === PegaConstants.TOP_ACTION_LABEL.OPENASSIGNMENT && action.assignmentLabel === undefined) {
      action.assignmentLabel =
        response?.data?.data?.caseInfo?.assignments.find(
          (assignment: Record<string, string>) => assignment.ID === assignmentID
        )?.name ?? '';
    }
  }

  /**
   * Updates DX api object when on success/failure response
   * @description This method identifies for which DX api call response is returned and updates responseBytes/status/total time of DX api call
   * @param {number} serviceCallIndex - DX api calls are maintained in array, this index refers the location where current DX api call needs to be mapped
   * @param {number} actionIndex - index of the top level action where DX api call is maintained
   * @param {boolean} isCancelled - boolean true or false based on where particular DX api call is cancelled or not
   * @param {AxiosResponse|AxiosError} response - DX api response or error
   *
   * @example <caption>Example for #populateAdditionalActionInfo</caption>
   * Example can be seen in Performance manager, private method.
   * Example usage - this.#populateAdditionalActionInfo(0, 1, false, response) - DX api response
   *
   * @see {@link #populateAdditionalActionInfo} for further information.
   * @returns {void}
   * @function
   */
  #populateAdditionalActionInfo(
    serviceCallIndex: number,
    actionIndex: number,
    isCancelled: boolean,
    response: AxiosResponse | AxiosError
  ): void {
    if (serviceCallIndex < 0) {
      return;
    }
    const serviceCall = this.#actionsMasterList[actionIndex].serviceCalls[serviceCallIndex];
    const currentTime = Date.now();
    serviceCall.endTime = currentTime;
    serviceCall.totalTimeTaken = currentTime - serviceCall.startTime;
    serviceCall.httpMethod = response.config?.method;

    if (isCancelled) {
      serviceCall.status = 'CANCELLED';
    } else if ('headers' in response) {
      serviceCall.status = response.status;
      serviceCall.responseBytes = response.headers['content-length'];
    } else {
      // else block is added to fix sonar lint
    }
  }

  /**
   * Prepares top level action object
   * @description This method prepares top level action object and pushes to master list where all top level action is maintained
   * @param {string} url - url of DX api call
   * @param {string} actionLabel - label of any top level action like Create-Work, Open-Assignment etc
   * @param {boolean} topLevelAction - boolean true or false based on where particular DX api call belongs to top level user action or not
   * @param {string} context - context where DX api call is invoked
   * @param {ServiceCall} serviceCall - serviceCall object that stores start/end time, response status etc
   *
   * @example <caption>Example for #prepareAction</caption>
   * Example can be seen in Performance manager, private method.
   * Example usage - this.#prepareAction('https://localhost:32424/prweb/app/react//api/application/v2/cases?viewType=page', 'Create-Work', true,
   * 'app/primary1/workarea1', {
   *  url: 'https://localhost:32424/prweb/app/react//api/application/v2/cases?viewType=page',
   *  startTime: Date.now(),
   *  context: 'app/primary1/workarea1',
   *  category: PerformanceManager.getCategory('https://localhost:32424/prweb/app/react//api/application/v2/cases?viewType=page')
   * })
   *
   * @see {@link #prepareAction} for further information.
   * @returns {Record<string, string | boolean>} - contains actionUniqueID (unique id attached to top level action), urlUniqueID (unique id attached to individual DX api call), topLevelAction (whether top level action or not)
   * @function
   */
  #prepareAction(
    url: string,
    actionLabel: string,
    topLevelAction: boolean,
    context: string,
    serviceCall: ServiceCall
  ): Record<string, string | boolean> {
    let actionID = '';
    if (!topLevelAction) {
      // @ts-expect-error
      const matched = url.match(PerformanceManager.portalRegex)[0];
      actionID = matched?.split(' ')[0];
    }

    const uniqueString = `${actionLabel || actionID}${PegaConstants.HASH_DELIMITER}${serviceCall.startTime}`;
    const uniqueID = Utils.getHashedString(uniqueString);
    serviceCall.urlUniqueID = uniqueID;
    const actionConfig = {
      label: actionLabel || `${PerformanceManager.getPortalLabel(actionID, context)} Portal`,
      actionUniqueID: uniqueID,
      serviceCalls: [serviceCall]
    };
    this.#actionsMasterList.push(actionConfig);
    return {
      actionUniqueID: uniqueID,
      urlUniqueID: uniqueID,
      topLevelAction
    };
  }

  /**
   * Updates case ID/label and assignment label
   * @description This method identifies whether case ID/label and assignment label are present in response, populate accordingly
   * @param {number} actionIndex - index of the top level action where DX api call is maintained
   * @param {string} assignmentID - assignment ID during open assignment
   * @param {AxiosResponse} response - DX api response
   *
   * @example <caption>Example for #populateCaseDetails</caption>
   * Example can be seen in Performance manager, private method.
   * Example usage - this.#populateCaseDetails(0, 'ASSIGN-WORKLIST O8EQL4-DAYS8REACT-WORK S-3016!CREATEFORM_DEFAULT', response) - DX api response
   *
   * @see {@link #populateAdditionalActionInfo} for further information.
   * @returns {void}
   * @function
   */
  #populateCaseDetails(actionIndex: number, assignmentID: string, response: AxiosResponse) {
    const action = this.#actionsMasterList[actionIndex];
    PerformanceManager.extractAssignmentLabel(action, assignmentID, response);

    const caseContent = response.data?.data?.caseInfo?.content;
    if (caseContent) {
      action.caseLabel = caseContent.pyLabel;
      action.caseID = caseContent.pyID;
    }
  }

  /**
   * Updates DX api object when on success/failure response
   * @description This method identifies for which DX api call response is returned and updates responseBytes/status/total time of DX api call
   * @param {ActionsInfo} actions - actionInfo object passed down from service broker layer
   *
   * @example <caption>Example for buildActionsList</caption>
   * Example can be seen in service broker.
   * Example usage - PerformanceManager.buildActionsList({
   *    url: 'https://localhost:32424/prweb/app/react//api/application/v2/cases?viewType=page',
   *    topLevelAction: true,
   *    userAction: true,
   *    actionLabel: 'Create-Work',
   *    context: 'app/primary1'
   *  })
   *
   * @see {@link buildActionsList} for further information.
   * @returns {Record<string, string | boolean>} - contains actionUniqueID (unique id attached to top level action), urlUniqueID (unique id attached to individual DX api call), topLevelAction (whether top level action or not)
   * @function
   */
  buildActionsList(actions: ActionsInfo): Record<string, string | boolean> {
    const { url, topLevelAction, userAction, actionLabel, context } = actions;
    let additionalInfo: Record<string, string | boolean> = {};

    if (this.#togglePerformanceLog) {
      const serviceCall: ServiceCall = {
        url: url.split(Services.getServerURL() ?? '')[1],
        startTime: Date.now(),
        context,
        category: PerformanceManager.getCategory(url)
      };
      if (topLevelAction && userAction) {
        additionalInfo = this.#prepareAction(url, actionLabel, true, context, serviceCall);
      } else if (PerformanceManager.portalRegex.test(url)) {
        additionalInfo = this.#prepareAction(url, actionLabel, false, context, serviceCall);
      } else if (this.#actionsMasterList.length > 0) {
        const serviceCalls = this.#actionsMasterList[this.#actionsMasterList.length - 1].serviceCalls;
        const uniqueString = `${url}${PegaConstants.HASH_DELIMITER}${serviceCall.startTime}${PegaConstants.HASH_DELIMITER}${serviceCalls.length}`;
        const uniqueID = Utils.getHashedString(uniqueString);
        serviceCall.urlUniqueID = uniqueID;
        serviceCalls.push(serviceCall);
        additionalInfo.actionUniqueID = this.#actionsMasterList[this.#actionsMasterList.length - 1].actionUniqueID;
        additionalInfo.urlUniqueID = uniqueID;
        additionalInfo.topLevelAction = topLevelAction;
      } else {
        // else block is added to fix sonar lint
      }
    }
    return additionalInfo;
  }

  /**
   * Transforms top level action object and individual serviceCall object
   * @description This method identifies particular DX api serviceCall object and top level config object where it belongs to and transform these objects with data like caseID/label, total time, response status for serviceCalls
   * @param {string} url - url of DX api call
   * @param {AxiosResponse|AxiosError} response - DX api response or error
   * @param {boolean} success - boolean true or false based on whether DX api is a success or failure
   * @param {boolean} isCancelled - boolean true or false based on where particular DX api call is cancelled or not
   *
   * @example <caption>Example for reformActionList</caption>
   * Example can be seen in service broker.
   * Example usage - PerformanceManager.reformActionList('https://localhost:32424/prweb/app/react//api/application/v2/cases?viewType=page', response, true, false)
   *
   * @see {@link reformActionList} for further information.
   * @returns {void}
   * @function
   */
  reformActionList(url: string, response: AxiosResponse | AxiosError, success: boolean, isCancelled: boolean): void {
    const config = response.config as ResponseConfig;
    const { actionUniqueID, urlUniqueID, topLevelAction, assignmentID } = config;
    if (this.#togglePerformanceLog) {
      const actionIndex = this.#actionsMasterList.findIndex(
        (action: Action) => action.actionUniqueID === actionUniqueID
      );
      if (actionIndex >= 0) {
        if (topLevelAction && success && 'data' in response) {
          this.#populateCaseDetails(actionIndex, assignmentID, response);
        }
        url = url.split(Services.getServerURL() ?? '')[1];
        const serviceCallIndex = this.#actionsMasterList[actionIndex].serviceCalls.findIndex(
          (serviceCall: ServiceCall) => serviceCall.url === url && serviceCall.urlUniqueID === urlUniqueID
        );
        this.#populateAdditionalActionInfo(serviceCallIndex, actionIndex, isCancelled, response);
      }
    }
  }

  /**
   * Updates user actions to localStorage
   * @description This method prepares user actions data and stores onto localStorage
   *
   * @example <caption>Example for #updateLocalStorage</caption>
   * This is a private method
   * Example usage - this.#updateLocalStorage() from Performance manager constructor when recording is stopped
   *
   * @see {@link #updateLocalStorage} for further information.
   * @returns {void}
   * @function
   */
  #updateLocalStorage(): void {
    if (this.#actionsMasterList.length === 0) {
      return;
    }
    const userActionsData = {
      version: '1.0.0',
      recordedTime: this.#endTime - this.#startTime,
      totalActions: this.#actionsMasterList.length,
      startTime: this.#startTime,
      userActions: this.#actionsMasterList
    };
    localStorage.setItem('performanceData', JSON.stringify(userActionsData));
  }

  /**
   * Clears user actions from local storage and set actionList to empty array
   * @description This method clears entire user actions from localStorage and re-initializes actionList which actually holds all user action in JS memory
   *
   * @example <caption>Example for clearStorage</caption>
   * Example can be seen in Performance dashboard
   * Example usage - PCore.getPerformanceManager().clearStorage()
   *
   * @see {@link clearStorage} for further information.
   * @returns {void}
   * @function
   */
  clearStorage(): void {
    localStorage.removeItem('performanceData');
    this.#actionsMasterList = [];
  }
}

export default new PerformanceManager();
