import { from } from 'rxjs';
import { map } from 'rxjs/operators';

import { resolveRestApi } from '../../router/app-router';
import { fetch } from '../../service_broker/fetch';
import * as actionType from '../../store/state/actions';
import { processHeaders } from '../../store/state/epics/util';
import { logAndCatchError } from '../../utils/rxjs-utils';
import Constants from '../../constants';
import EnvironmentInfo from '../../environment-info';
import performanceUtils from '../../utils/performance-utils';

const DXAPI_ERROR = 'Error occurred during ajax call at showData Epic : ';

const executeAsyncRequest = (
  payload: {
    context: any;
    pageName?: any;
    dataContext?: any;
    dataContextParameters?: any;
    semanticURL?: any;
    skipSemanticUrl?: any;
    containerName?: any;
    isDeferLoaded?: any;
    parentAction?: any;
    resourceType?: any;
  },
  options: { removeActionPayload: any }
) => {
  const { context, pageName, dataContext, dataContextParameters, semanticURL, skipSemanticUrl } = payload;
  const { removeActionPayload } = options;
  const {
    restApi: restUrl,
    reqType,
    body
  } = resolveRestApi(actionType.ACTION_SHOWDATA, { pageName, dataContext, dataContextParameters });
  let actionInfo: {
    topLevelAction?: boolean;
    actionLabel?: string;
  } = {};
  if (EnvironmentInfo.getPerformanceDashboardVisibility()) {
    // topLevelAction and actionLabel are used in Performance manager module to capture user actions
    actionInfo = performanceUtils.getActionInfo(Constants.TOP_ACTION_LABEL.SHOWDATA);
  }

  return from(
    fetch(
      restUrl,
      {
        method: reqType,
        body
      },
      context,
      actionInfo
    )
  ).pipe(
    map((response) => {
      const responseData = response.data;
      return {
        response: {
          ...responseData,
          key: semanticURL,
          semanticURL
        },
        actionConfig: payload,
        headers: processHeaders(response.headers),
        removeActionPayload,
        skipSemanticUrl
      };
    }),
    logAndCatchError(DXAPI_ERROR, payload.context)
  );
};

export default executeAsyncRequest;
